import { serverTimestamp, Timestamp } from 'firebase/firestore';
import {
  employeesService,
  departmentsService,
  employeeRolesService,
  leadSourcesService,
  productsService,
  leadsService,
  quotationsService,
  quotationTemplatesService
} from './business-services';

// Import JSON data
import employeesData from './data/employees.json';
import departmentsData from './data/departments.json';
import employeeRolesData from './data/employee-roles.json';
import leadSourcesData from './data/lead-sources.json';
import productsData from './data/products.json';
import leadsData from './data/leads.json';
import quotationsData from './data/quotations.json';
import quotationTemplatesData from './data/quotation-templates.json';

interface MigrationResult {
  collection: string;
  success: number;
  failed: number;
  errors: string[];
}

interface MigrationSummary {
  totalCollections: number;
  totalDocuments: number;
  successfulDocuments: number;
  failedDocuments: number;
  results: MigrationResult[];
  startTime: Date;
  endTime?: Date;
  duration?: number;
}

export class FirestoreMigration {
  private results: MigrationResult[] = [];
  private summary: MigrationSummary;

  constructor() {
    this.summary = {
      totalCollections: 0,
      totalDocuments: 0,
      successfulDocuments: 0,
      failedDocuments: 0,
      results: [],
      startTime: new Date()
    };
  }

  // Helper function to convert date strings to Firestore timestamps
  private convertToTimestamp(dateString: string | undefined): Timestamp | undefined {
    if (!dateString) return undefined;
    try {
      return Timestamp.fromDate(new Date(dateString));
    } catch (error) {
      console.warn('Failed to convert date:', dateString);
      return undefined;
    }
  }

  // Helper function to generate new ID if needed
  private generateId(prefix: string): string {
    return `${prefix}-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  }

  // Migrate employees
  async migrateEmployees(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'employees',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating employees...');

    for (const employee of employeesData) {
      try {
        const employeeData = {
          ...employee,
          createdAt: this.convertToTimestamp(employee.createdAt) || serverTimestamp(),
          updatedAt: serverTimestamp(),
          isActive: true // Default to active
        };

        // Use existing ID or generate new one
        const id = employee.id || this.generateId('emp');
        await employeesService.createWithId(id, employeeData);
        
        result.success++;
        console.log(` Migrated employee: ${employee.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate employee ${employee.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate departments
  async migrateDepartments(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'departments',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating departments...');

    for (const department of departmentsData) {
      try {
        const departmentData = {
          ...department,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp()
        };

        const id = department.id || this.generateId('dept');
        await departmentsService.createWithId(id, departmentData);
        
        result.success++;
        console.log(` Migrated department: ${department.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate department ${department.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate employee roles
  async migrateEmployeeRoles(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'employeeRoles',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating employee roles...');

    for (const role of employeeRolesData) {
      try {
        const roleData = {
          ...role,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp()
        };

        const id = role.id || this.generateId('role');
        await employeeRolesService.createWithId(id, roleData);
        
        result.success++;
        console.log(` Migrated role: ${role.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate role ${role.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate lead sources
  async migrateLeadSources(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'leadSources',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating lead sources...');

    for (const source of leadSourcesData) {
      try {
        const sourceData = {
          ...source,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
          isActive: true
        };

        const id = source.id || this.generateId('source');
        await leadSourcesService.createWithId(id, sourceData);
        
        result.success++;
        console.log(` Migrated lead source: ${source.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate lead source ${source.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate products
  async migrateProducts(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'products',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating products...');

    for (const product of productsData) {
      try {
        const productData = {
          ...product,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
          isActive: true,
          stockQuantity: 100, // Default stock
          minStockLevel: 10   // Default minimum stock
        };

        const id = product.id || this.generateId('prod');
        await productsService.createWithId(id, productData);
        
        result.success++;
        console.log(` Migrated product: ${product.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate product ${product.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate leads
  async migrateLeads(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'leads',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating leads...');

    for (const lead of leadsData) {
      try {
        // Convert activities timestamps
        const activities = lead.activities?.map(activity => ({
          ...activity,
          type: activity.type as any, // Cast activity type
          date: this.convertToTimestamp(activity.date) || activity.date
        })) || [];

        const leadData = {
          ...lead,
          status: lead.status as any, // Cast to avoid TypeScript strict checking during migration
          createdAt: this.convertToTimestamp(lead.createdAt) || serverTimestamp(),
          updatedAt: serverTimestamp(),
          activities,
          priority: 'Medium' as const, // Default priority
          estimatedValue: lead.products?.reduce((sum, product) => 
            sum + (product.quantity * product.rate), 0) || 0
        };

        const id = lead.id || this.generateId('lead');
        await leadsService.createWithId(id, leadData);
        
        result.success++;
        console.log(` Migrated lead: ${lead.name} (${lead.company})`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate lead ${lead.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate quotations
  async migrateQuotations(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'quotations',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating quotations...');

    for (const quotation of quotationsData) {
      try {
        const quotationData = {
          ...quotation,
          status: quotation.status as any, // Cast status for migration
          createdAt: this.convertToTimestamp(quotation.createdAt) || serverTimestamp(),
          updatedAt: serverTimestamp()
        };

        const id = quotation.id || this.generateId('quo');
        await quotationsService.createWithId(id, quotationData);
        
        result.success++;
        console.log(` Migrated quotation: ${quotation.quotationNumber}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate quotation ${quotation.quotationNumber}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Migrate quotation templates
  async migrateQuotationTemplates(): Promise<MigrationResult> {
    const result: MigrationResult = {
      collection: 'quotationTemplates',
      success: 0,
      failed: 0,
      errors: []
    };

    console.log(' Migrating quotation templates...');

    for (const template of quotationTemplatesData) {
      try {
        const templateData = {
          ...template,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
          isDefault: true // Mark as default template
        };

        const id = template.id || this.generateId('template');
        await quotationTemplatesService.createWithId(id, templateData);
        
        result.success++;
        console.log(` Migrated template: ${template.name}`);
      } catch (error) {
        result.failed++;
        const errorMsg = `Failed to migrate template ${template.name}: ${error}`;
        result.errors.push(errorMsg);
        console.error(` ${errorMsg}`);
      }
    }

    return result;
  }

  // Run complete migration
  async runMigration(): Promise<MigrationSummary> {
    console.log(' Starting Firestore migration...');
    console.log('  Make sure you have updated your Firestore security rules!');
    
    this.summary.startTime = new Date();

    try {
      // Run all migrations
      const migrations = [
        { name: 'Departments', fn: () => this.migrateDepartments() },
        { name: 'Employee Roles', fn: () => this.migrateEmployeeRoles() },
        { name: 'Lead Sources', fn: () => this.migrateLeadSources() },
        { name: 'Products', fn: () => this.migrateProducts() },
        { name: 'Employees', fn: () => this.migrateEmployees() },
        { name: 'Quotation Templates', fn: () => this.migrateQuotationTemplates() },
        { name: 'Leads', fn: () => this.migrateLeads() },
        { name: 'Quotations', fn: () => this.migrateQuotations() }
      ];

      for (const migration of migrations) {
        console.log(`\n Starting ${migration.name} migration...`);
        const result = await migration.fn();
        this.results.push(result);
        
        this.summary.totalDocuments += result.success + result.failed;
        this.summary.successfulDocuments += result.success;
        this.summary.failedDocuments += result.failed;
        
        console.log(` ${migration.name}: ${result.success} success, ${result.failed} failed`);
      }

      this.summary.totalCollections = migrations.length;
      this.summary.results = this.results;
      this.summary.endTime = new Date();
      this.summary.duration = this.summary.endTime.getTime() - this.summary.startTime.getTime();

      // Print summary
      console.log('\n Migration completed!');
      console.log(' Summary:');
      console.log(`   Collections: ${this.summary.totalCollections}`);
      console.log(`   Total Documents: ${this.summary.totalDocuments}`);
      console.log(`   Successful: ${this.summary.successfulDocuments}`);
      console.log(`   Failed: ${this.summary.failedDocuments}`);
      console.log(`   Duration: ${(this.summary.duration / 1000).toFixed(2)}s`);

      if (this.summary.failedDocuments > 0) {
        console.log('\n Errors encountered:');
        this.results.forEach(result => {
          if (result.errors.length > 0) {
            console.log(`\n${result.collection}:`);
            result.errors.forEach(error => console.log(`  - ${error}`));
          }
        });
      }

    } catch (error) {
      console.error(' Migration failed:', error);
      throw error;
    }

    return this.summary;
  }

  // Verify migration by checking document counts
  async verifyMigration(): Promise<void> {
    console.log('\n Verifying migration...');

    const verifications = [
      { name: 'Employees', service: employeesService, expected: employeesData.length },
      { name: 'Departments', service: departmentsService, expected: departmentsData.length },
      { name: 'Employee Roles', service: employeeRolesService, expected: employeeRolesData.length },
      { name: 'Lead Sources', service: leadSourcesService, expected: leadSourcesData.length },
      { name: 'Products', service: productsService, expected: productsData.length },
      { name: 'Leads', service: leadsService, expected: leadsData.length },
      { name: 'Quotations', service: quotationsService, expected: quotationsData.length },
      { name: 'Quotation Templates', service: quotationTemplatesService, expected: quotationTemplatesData.length }
    ];

    for (const verification of verifications) {
      try {
        const documents = await verification.service.getAll();
        const actual = documents.length;
        const status = actual === verification.expected ? '' : '';
        
        console.log(`${status} ${verification.name}: ${actual}/${verification.expected}`);
        
        if (actual !== verification.expected) {
          console.warn(`   Expected ${verification.expected} but found ${actual}`);
        }
      } catch (error) {
        console.error(` Failed to verify ${verification.name}:`, error);
      }
    }
  }

  // Clear all migrated data (use with caution!)
  async clearAllData(): Promise<void> {
    console.log('  Clearing all migrated data...');
    console.warn('  This will delete ALL data from Firestore!');

    const services = [
      employeesService,
      departmentsService,
      employeeRolesService,
      leadSourcesService,
      productsService,
      leadsService,
      quotationsService,
      quotationTemplatesService
    ];

    for (const service of services) {
      try {
        const documents = await service.getAll();
        const ids = documents.map(doc => doc.id!);
        
        if (ids.length > 0) {
          await service.deleteMultiple(ids);
          console.log(` Cleared ${ids.length} documents from ${service['collectionName']}`);
        }
      } catch (error) {
        console.error(` Failed to clear ${service['collectionName']}:`, error);
      }
    }
  }
}

// Export singleton instance
export const firestoreMigration = new FirestoreMigration();
