'use client';

import React, { useState } from 'react';
import { firestoreMigration } from '../lib/migrate-to-firestore';

interface MigrationResult {
  collection: string;
  success: number;
  failed: number;
  errors: string[];
}

interface MigrationSummary {
  totalCollections: number;
  totalDocuments: number;
  successfulDocuments: number;
  failedDocuments: number;
  results: MigrationResult[];
  startTime: Date;
  endTime?: Date;
  duration?: number;
}

export default function FirestoreMigration() {
  const [isRunning, setIsRunning] = useState(false);
  const [isVerifying, setIsVerifying] = useState(false);
  const [isClearing, setIsClearing] = useState(false);
  const [summary, setSummary] = useState<MigrationSummary | null>(null);
  const [logs, setLogs] = useState<string[]>([]);
  const [showLogs, setShowLogs] = useState(false);

  const addLog = (message: string) => {
    setLogs(prev => [...prev, `${new Date().toLocaleTimeString()}: ${message}`]);
  };

  const runMigration = async () => {
    setIsRunning(true);
    setLogs([]);
    setSummary(null);
    
    try {
      addLog(' Starting migration...');
      
      // Override console.log to capture logs
      const originalLog = console.log;
      const originalError = console.error;
      const originalWarn = console.warn;
      
      console.log = (...args) => {
        addLog(args.join(' '));
        originalLog(...args);
      };
      
      console.error = (...args) => {
        addLog(`ERROR: ${args.join(' ')}`);
        originalError(...args);
      };
      
      console.warn = (...args) => {
        addLog(`WARN: ${args.join(' ')}`);
        originalWarn(...args);
      };

      const result = await firestoreMigration.runMigration();
      setSummary(result);
      
      // Restore console methods
      console.log = originalLog;
      console.error = originalError;
      console.warn = originalWarn;
      
      addLog(' Migration completed successfully!');
    } catch (error) {
      addLog(` Migration failed: ${error}`);
      console.error('Migration error:', error);
    } finally {
      setIsRunning(false);
    }
  };

  const verifyMigration = async () => {
    setIsVerifying(true);
    
    try {
      addLog(' Starting verification...');
      await firestoreMigration.verifyMigration();
      addLog(' Verification completed!');
    } catch (error) {
      addLog(` Verification failed: ${error}`);
      console.error('Verification error:', error);
    } finally {
      setIsVerifying(false);
    }
  };

  const clearAllData = async () => {
    if (!confirm(' This will delete ALL migrated data from Firestore. Are you sure?')) {
      return;
    }
    
    if (!confirm(' FINAL WARNING: This action cannot be undone. Continue?')) {
      return;
    }
    
    setIsClearing(true);
    
    try {
      addLog(' Starting data cleanup...');
      await firestoreMigration.clearAllData();
      addLog(' Data cleanup completed!');
      setSummary(null);
    } catch (error) {
      addLog(` Data cleanup failed: ${error}`);
      console.error('Cleanup error:', error);
    } finally {
      setIsClearing(false);
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6 bg-white">
      <h1 className="text-3xl font-bold mb-6 text-center"> Firestore Data Migration</h1>
      
      {/* Status Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div className="bg-blue-50 p-4 rounded-lg">
          <h3 className="font-semibold text-blue-800"> Data Sources</h3>
          <ul className="text-sm text-blue-600 mt-2">
            <li> Employees</li>
            <li> Departments</li>
            <li> Employee Roles</li>
            <li> Lead Sources</li>
            <li> Products</li>
            <li> Leads</li>
            <li> Quotations</li>
            <li> Quotation Templates</li>
          </ul>
        </div>
        
        <div className="bg-green-50 p-4 rounded-lg">
          <h3 className="font-semibold text-green-800"> Target Collections</h3>
          <ul className="text-sm text-green-600 mt-2">
            <li> employees</li>
            <li> departments</li>
            <li> employeeRoles</li>
            <li> leadSources</li>
            <li> products</li>
            <li> leads</li>
            <li> quotations</li>
            <li> quotationTemplates</li>
          </ul>
        </div>
        
        <div className="bg-yellow-50 p-4 rounded-lg">
          <h3 className="font-semibold text-yellow-800"> Prerequisites</h3>
          <ul className="text-sm text-yellow-600 mt-2">
            <li> Firestore security rules updated</li>
            <li> Authentication enabled</li>
            <li> Network connection stable</li>
            <li> Backup existing data</li>
          </ul>
        </div>
      </div>

      {/* Action Buttons */}
      <div className="flex flex-wrap gap-4 mb-6">
        <button
          onClick={runMigration}
          disabled={isRunning}
          className="bg-blue-500 text-white px-6 py-3 rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
        >
          {isRunning ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              Running Migration...
            </>
          ) : (
            ' Start Migration'
          )}
        </button>
        
        <button
          onClick={verifyMigration}
          disabled={isVerifying || !summary}
          className="bg-green-500 text-white px-6 py-3 rounded-lg hover:bg-green-600 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
        >
          {isVerifying ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              Verifying...
            </>
          ) : (
            ' Verify Migration'
          )}
        </button>
        
        <button
          onClick={clearAllData}
          disabled={isClearing}
          className="bg-red-500 text-white px-6 py-3 rounded-lg hover:bg-red-600 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
        >
          {isClearing ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              Clearing...
            </>
          ) : (
            ' Clear All Data'
          )}
        </button>
        
        <button
          onClick={() => setShowLogs(!showLogs)}
          className="bg-gray-500 text-white px-6 py-3 rounded-lg hover:bg-gray-600"
        >
          {showLogs ? ' Hide Logs' : ' Show Logs'}
        </button>
      </div>

      {/* Migration Summary */}
      {summary && (
        <div className="bg-gray-50 p-6 rounded-lg mb-6">
          <h2 className="text-xl font-semibold mb-4"> Migration Summary</h2>
          
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-4">
            <div className="text-center">
              <div className="text-2xl font-bold text-blue-600">{summary.totalCollections}</div>
              <div className="text-sm text-gray-600">Collections</div>
            </div>
            <div className="text-center">
              <div className="text-2xl font-bold text-green-600">{summary.successfulDocuments}</div>
              <div className="text-sm text-gray-600">Successful</div>
            </div>
            <div className="text-center">
              <div className="text-2xl font-bold text-red-600">{summary.failedDocuments}</div>
              <div className="text-sm text-gray-600">Failed</div>
            </div>
            <div className="text-center">
              <div className="text-2xl font-bold text-purple-600">
                {summary.duration ? `${(summary.duration / 1000).toFixed(1)}s` : 'N/A'}
              </div>
              <div className="text-sm text-gray-600">Duration</div>
            </div>
          </div>

          {/* Detailed Results */}
          <div className="space-y-2">
            <h3 className="font-semibold">Collection Results:</h3>
            {summary.results.map((result, index) => (
              <div key={index} className="flex justify-between items-center p-2 bg-white rounded">
                <span className="font-medium">{result.collection}</span>
                <div className="flex space-x-4 text-sm">
                  <span className="text-green-600"> {result.success}</span>
                  <span className="text-red-600"> {result.failed}</span>
                </div>
              </div>
            ))}
          </div>

          {/* Errors */}
          {summary.failedDocuments > 0 && (
            <div className="mt-4">
              <h3 className="font-semibold text-red-600 mb-2"> Errors:</h3>
              <div className="bg-red-50 p-3 rounded max-h-40 overflow-y-auto">
                {summary.results.map((result, index) => 
                  result.errors.map((error, errorIndex) => (
                    <div key={`${index}-${errorIndex}`} className="text-sm text-red-700 mb-1">
                      <strong>{result.collection}:</strong> {error}
                    </div>
                  ))
                )}
              </div>
            </div>
          )}
        </div>
      )}

      {/* Logs */}
      {showLogs && (
        <div className="bg-black text-green-400 p-4 rounded-lg font-mono text-sm">
          <div className="flex justify-between items-center mb-2">
            <h3 className="font-semibold"> Migration Logs</h3>
            <button
              onClick={() => setLogs([])}
              className="text-red-400 hover:text-red-300 text-xs"
            >
              Clear Logs
            </button>
          </div>
          <div className="max-h-96 overflow-y-auto">
            {logs.length === 0 ? (
              <div className="text-gray-500">No logs yet...</div>
            ) : (
              logs.map((log, index) => (
                <div key={index} className="mb-1">
                  {log}
                </div>
              ))
            )}
          </div>
        </div>
      )}

      {/* Instructions */}
      <div className="mt-8 bg-blue-50 p-6 rounded-lg">
        <h3 className="text-lg font-semibold mb-4"> Migration Instructions</h3>
        
        <div className="space-y-4">
          <div>
            <h4 className="font-semibold text-blue-800">Before Migration:</h4>
            <ol className="list-decimal list-inside text-sm text-blue-700 ml-4">
              <li>Update Firestore security rules to allow writes</li>
              <li>Ensure you have a stable internet connection</li>
              <li>Backup your existing Firestore data (if any)</li>
              <li>Review the data sources and target collections</li>
            </ol>
          </div>
          
          <div>
            <h4 className="font-semibold text-blue-800">During Migration:</h4>
            <ul className="list-disc list-inside text-sm text-blue-700 ml-4">
              <li>Do not close the browser tab</li>
              <li>Monitor the logs for any errors</li>
              <li>The process may take several minutes</li>
            </ul>
          </div>
          
          <div>
            <h4 className="font-semibold text-blue-800">After Migration:</h4>
            <ul className="list-disc list-inside text-sm text-blue-700 ml-4">
              <li>Run verification to ensure data integrity</li>
              <li>Update your application code to use Firestore services</li>
              <li>Test all functionality thoroughly</li>
              <li>Update security rules for production use</li>
            </ul>
          </div>
        </div>
      </div>

      {/* Security Rules Reminder */}
      <div className="mt-6 bg-yellow-50 border-l-4 border-yellow-400 p-4">
        <div className="flex">
          <div className="flex-shrink-0">
            <span className="text-yellow-400"></span>
          </div>
          <div className="ml-3">
            <p className="text-sm text-yellow-700">
              <strong>Security Rules Required:</strong> Make sure you have updated your Firestore security rules 
              to allow write operations. You can use the testing rules provided in the documentation, 
              but remember to update them for production use.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
